import React, { useState, useEffect } from 'react'
import { registerUser } from '../../actions/userActions'
import { useNavigate } from 'react-router-dom'
import UploadImage from '../UploadImage'
import { useAppDispatch } from '../../hooks/hooks'

const names = [
  { name: 'nickname', type: 'text', label: '' },
  { name: 'email', type: 'email', label: '' },
  { name: 'password', type: 'password', label: '' },
  { name: 'repassword', type: 'password', label: ' ' },
]

export const AuthPage: React.FC = () => {
  const dispatch = useAppDispatch()
  const [formValues, setFormValues] = useState({
    nickname: '',
    email: '',
    password: '',
    repassword: '',
    birthday: '',
    img: '',
    sex: 0,
  })

  const [image, setImage] = useState('')

  useEffect(() => {
    setFormValues((prevState) => ({ ...prevState, ['img']: image }))
  }, [image])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target

    setFormValues((prevState) => ({ ...prevState, [name]: value }))
  }

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()

    console.log(formValues)
    dispatch(registerUser(formValues))
  }

  return (
    <div className='bg-slate-200 h-[calc(100vh_-_65px)] flex flex-row align-middle justify-center'>
      <div className='text-center w-[333px] h-[600px] rounded-md my-auto mx-2 bg-white flex flex-col p-4'>
        <span className='text-xl mt-2'></span>
        <UploadImage
          className='mx-auto mt-12 h-[240px] w-[240px]'
          value={formValues.img}
          onChange={setImage}
        />
        <span className='my-4'>JPG  PNG   5 </span>
        <span>
            <b></b>,   
        </span>
      </div>
      <div className='w-[564px] h-[600px] rounded-md my-auto mx-2 bg-white flex flex-col p-4'>
        <label className='block text-xl'>
           ?
          <br />
          <span className='text-sm'>
                   
          </span>
        </label>
        <form onSubmit={handleSubmit} className='flex flex-col mt-6'>
          {names.map((item) => (
            <div key={item.name}>
              <div className='tracking-wide text-gray-700 text-md mb-1'>{item.label}</div>
              <input
                className={
                  'appearance-none block w-full bg-white text-gray-700 border border-gray-300 rounded-md py-2 px-4 mb-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500'
                }
                onChange={handleChange}
                type={item.type}
                name={item.name}
                placeholder={item.label}
              />
            </div>
          ))}
          <div className='flex flex-row justify-between'>
            <div className='w-[240px]'>
              <div className='tracking-wide text-gray-700 text-md mb-1'> </div>
              <input
                className='appearance-none block w-full bg-white text-gray-700 border border-gray-300 rounded-md py-2 px-4 mb-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500'
                onChange={handleChange}
                type='date'
                name='birthday'
                placeholder=' '
              />
            </div>
            <div>
              <div className='tracking-wide text-gray-700 text-md mb-1'></div>
            </div>
          </div>
          <button
            type='submit'
            className='text-white w-36 mx-auto bg-cred hover:bg-blue-800 focus:ring-4 focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 mb-2'
          >
            
          </button>
        </form>
      </div>
    </div>
  )
}
